/* Colores */
$black:#000;
$white: #fff;

/* Mixins */
@mixin separate ($top:0rem, $right:0rem, $botton:2.5rem, $left: 0rem) {
    padding: $top $right $botton $left;
}
@mixin bg-color ($color){
    background-color: $color
} 

html {
    font-size: 62.5%;
    box-sizing: border-box;

}
*,
*::before,
*::after {
    box-sizing: inherit;
}

body{
    font-family: 'Montserrat', sans-serif;
}

/* Fondo */
.bg-color {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(45deg, rgba(41,73,84,1) 0%, rgba(78,90,68,1) 100%);
    position: relative;

    &::before{
        content: "nike";
        position: absolute;
        transform: rotate(-90deg);
        left: 3rem;
        bottom: 25rem;
        font-size: 30rem;
        font-weight: 900;
        text-transform: uppercase;
        color: rgba(41, 73, 84, 0.7);
    }
}

.container{
    width: 100%;
    height: 100%;
    max-width: 120rem;
    margin: 0 auto;

}

.products{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.product-img{
    background: linear-gradient(45deg, rgba(0,140,185,1) 0%, rgba(132,184,101,1) 100%);
    flex-basis: 40rem;
    max-height: 50rem;
    height: 100%;
    position: relative;
    overflow: hidden;

    & p{
        margin: 0;
        position: absolute;
        bottom: 0;
        color: #43c7ce;
        font-size: 20rem;
        padding: 0 2rem;
        letter-spacing: 2rem;
        font-weight: 600;     
    }

    & img{
        transform: translate(-20rem, -10rem);
    }
    

}

.product-content{
    @include separate($top:3rem, $right:4rem, $botton:2rem, $left: 4rem);
    background-color: $white;
    flex-basis: 50rem;
    max-height: 60rem;
    height: 100%;
}

.product-header{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    @include separate;

    & .product-value{
        font-size: 2rem;
        color:#f3cd52;

    }
    & .product-price__number{
        margin: 0;
        font-size: 5rem;
        position: relative;  
    }
    & .product-price__number::before{
        content: "$";
        font-size: 3rem;
        position: absolute;
        left: -2rem;
    }
}

.product-name{
    @include separate;
    & h2{
        font-size: 4rem;
        margin: 0;
        line-height: 1;
    }
    & p{
        font-size: 3rem;
        margin:0;
    }
}

.product-color{
    width: 65%;
    h2{
        text-transform: uppercase;
        margin: 0;
        line-height: 2;
        font-size: 2rem;
        color: #858383;
    }
}

.colored-circles{
    @include separate($botton:2.5rem);
    display: flex;
    justify-content: space-around;
    
    
    & .circle{
        height: 3.5rem;
        flex-basis: calc(20% - 2rem);
        border-radius: 50%;
    }
    & .circle:nth-child(1){
        @include bg-color(#27fafa);
    }
    & .circle:nth-child(2){
        @include bg-color(#072cfc);
    }
    & .circle:nth-child(3){
        @include bg-color(green);
    }
    & .circle:nth-child(4){
        @include bg-color(#27fafa);
    }
    & .circle:nth-child(5){
        @include bg-color(#27fafa);
    }
}

.product-size{
    width: 40%;
    margin-bottom: 4rem;

    & h2{
        text-transform: uppercase;
        font-size: 2rem;
        margin: 0;
        line-height: 2;
        color: #858383;
    }

}
.products-number{
    display: flex;
    border: 1px solid #8583833f;;
    border-radius: 10rem;

    & .product-size__items{
        text-align: center;
        font-size: 3rem;
        flex-basis: calc(33.3%);   
    }
    & .product-size__items:nth-child(1),
    & .product-size__items:nth-child(2) {
        border-right: 1px solid #8583833f;;
        padding: .5rem;
    }

    & .product-size__items:nth-child(3){
        padding: .5rem;
    }
}

.product-buy{
    display: flex;
    flex-direction: column;
    align-items: center;

    & .btn{
        width: 100%;
        margin-bottom: 2rem;
        border:none;
        border-radius: 4rem;
        padding: 2rem 0;
        background-color: #1f40fc;
        font-size: 1.5rem;
        color:$white;
        text-transform: uppercase;
        box-shadow: -1px 9px 5px 0px #1f40fc7e;
        margin-bottom: 2rem;
    }

    & a{
        font-size: 1.5rem;
        text-decoration: none;
        color: #27fafa;
        font-weight: 600;
    }
   
}








